<template>
  <div>
    <select v-model="dataRowItemOption.type" @change="setDataRowItemOption" class="ds-form-select data-row-options">
      <option value="yes/no">yes/no</option>
      <option value="Yes/No">Yes/No</option>
      <option value="true/false">true/false</option>
      <option value="True/False">True/False</option>
    </select>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object,
  },
  setup(props) {
    const store = useStore();
    // default value
    let dataRowItemOption = props.dataRow ? computed(() => props.dataRow.item.itemOption) : ref({ type: 'yes/no' });

    // 선택한 data type을 store에 저장
    const setDataRowItemOption = () => {
      store.commit('setDataRowItemOption', {
        index: props.index,
        dataRowItemOption: dataRowItemOption.value,
      });
      console.log('dataRowItemOption -->', store.getters.getDataRowItemOption(props.index));
    };

    return {
      dataRowItemOption,
      setDataRowItemOption,
    };
  },
});
</script>

<style lang="scss" scoped>
.data-row-options {
  appearance: auto !important;
  width: auto;
}
</style>
